nav{

width:100%;
background-color: rgb(230, 253, 17);
height:50px;
margin-top: 60px;


}

div{

   padding:0px; 

}
.contenedorMenu{

   position: relative;
   height: 600px;
   width: 100%;
   z-index:17;
   margin-bottom:140px;
}

*{margin:0;padding:0;box-sizing:border-box;font-size:0;}  
nav a{
      text-decoration:none;
      color:inherit;
      font-size:14px;
      }
            
nav li{
      margin-top:10px;
      display:inline-block;
      width:220px;
      height:30px;
      padding:5px 10px;
      background-color:#000;
      border:1px solid #000;
      text-align:center;
      font-size:17px;
      color:#fff;
      position:relative;
}

nav li a{
   font-size:20px;

}
            
nav li:hover{
      background-color:#fff;
      color:#000;
      font-weight: bold;

      }
            
nav li>ul{
      display:none;
      }

nav li:hover>ul{
      display:block;
      position:absolute;
      top:30px;
      left:0px;
}

.ImgP{

      margin-top:30px;
}
