      .menuCSS3{
  
          border-radius: 30%;
      }
    .menuCSS3 ul {
      display: flex;
      padding: 0;
      margin: 0;
          list-style: none;
          border-radius: 30px;
    }
    .menuCSS3 a {
      display: block;
      padding: 2em;
      background-color: rgb(53, 52, 50);
      text-decoration: none;
      color: #191C26;
    }
    .menuCSS3 a:hover {
      background-color: rgb(29, 204, 29);
    }
    .menuCSS3 ul li ul {
      display: none;
    }
    .menuCSS3 ul li a:hover + ul{
      display: block;
    }
  
